* {
  box-sizing: border-box;
}

body {
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.homework-container {
  text-align: center;
  margin: 50px;
}

.container {
  border-top: 1px solid #0000001f;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

button {
  cursor: pointer;
  color: #fff;
  background-color: #f12ba5;
  border: none;
  border-radius: 5px;
  outline: none;
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

button:hover {
  background-color: #d1258f;
}

a {
  color: #fff;
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

